import { searchKeywords, garbageFeedback, uploadVoice } from '../../../api/index.js'
import SearchBox from '../../../components/SearchBox.vue'

export default{
	components: {
		SearchBox,
	},
	data(){
		return{
			keyword:undefined,
			step:1,
			isFadeback:false,
			openVoice:false,
			recorderManager:undefined
			
		}
	},
	onLoad() {
		this.initRecorderManager()
		// 询问授权
		this.auth()
		//验证版本
		this.validVersion()
	},
	methods:{
		auth(){
			let that = this
			uni.getSetting({
			  success: function (res) {
			    console.log(res)
			      if (!res.authSetting['scope.record']) {
			          uni.authorize({
			            scope: 'scope.record',
			            fail:()=>{
			              that.openVoice = false
			            }
			          })
			      }else{
			        that.openVoice = true
			      }
			  },
			  fail: function (res) {
			      uni.showToast({
			          title: '调用授权窗口失败',
			          icon: 'none',
			          duration: 1000
			      })
			  }
			})
		},
		authModel(){







			

			if(!this.canUseVoice){
				uni.showToast({
				    title: '您的微信版本较低，暂时无法使用该功能',
				    icon:"none"
				})
				return
			}

			
			let that = this
			uni.showModal({
			  title: '提示',
			  content: '是否开启录音授权',
			  success: function (tip) {
			    if (tip.confirm) {
			      uni.openSetting({
			        success: function (data) {
			            if (data.authSetting["scope.record"] === true) {
			                uni.showToast({
			                    title: '授权成功',
			                    icon: 'success',
			                    duration: 1000
			                })
			                //授权成功之后，
			                that.openVoice = true
			            } else {
			                uni.showToast({
			                    title: '授权失败',
			                    icon: 'success',
			                    duration: 1000
			                })
			                that.openVoice = false
			            }
			        }
			      })
			    }
			  }
			})
		},
		validVersion(){
		    uni.getSystemInfo({
		      complete: (res) => {
		        // console.log(res)
		        let version = res.version
		        // 1 a > b 
		        // 0 a = b
		        //-1 a < b
		        if(this.compareVersion(version, '7.0.8')==1){
		          this.canUseVoice = true
		        }else{
		          this.canUseVoice = false
		        }
		      },
		    })
		},
		initRecorderManager() {
		    const recorderManager = uni.getRecorderManager()
		    this.recorderManager = recorderManager
			
		    recorderManager.onStart(() => {
		      console.log('录音开始了')
		      this.step = 2
		    })
		
		    recorderManager.onInterruptionBegin(() => {
		      this.step = 4
		    })
		
		    recorderManager.onStop((res) => {
		      this.step = 3
		      // 识别录音
		      this.recognition(res) 
		    })
		
		    recorderManager.onError((err) => {
		      recorderManager.stop()
		    })
		},
		handleTouchStart(){
			//录音参数
			const options = {
			  duration: "60000",
			  sampleRate: 8000, //采样率
			  format: 'PCM' //音频格式
			  // format:"mp3"
			}
			//开启录音
			this.recorderManager.start(options);
		},
		handleTouchEnd(){
		    this.recorderManager.stop()
		},
		recognition(res) {
		    console.log('录音文件', res.tempFilePath)
		    const tempFilePath = res.tempFilePath
			uploadVoice(tempFilePath).then(result=>{
				
				if(res.code===0){
					// 接口连接失败
					this.step = 4
					return
				}
				
				let data = JSON.parse(result.data)
				let resultArr = data.result //识别结果
				
				if(resultArr){
					let text = resultArr[0].replace(/。/g,'')
					console.log('识别完成:',text);
					
					if(text.indexOf('我不知道')!=-1){
					  text = ''
					  // 结果不明白 未识别出来
					  this.step = 4
					  return
					}
					
					// 识别出来 模糊搜素关键字
					this.keyword = text
					that.fuzzySearch()
					
				}else{
				  //没有结果 未识别出来
				  this.step = 4
				}
			},reject=>{
				// 接口连接失败
				this.step = 4
			})
		},
		fuzzySearch(){
			searchKeywords(this.keyword).then(res=>{
				if(res.code==1){
					this.keywordsList = res.data
					this.step = 6
				}else{
					this.step = 5
				}
			})
		},
		linkTo(){
			uni.navigateTo({
				url:"/pages/Classification/search/index"
			})
		},
		chooseItem(item){
			// 弹出当前关联项
			console.log(item);
		},
		fadeback(){
			let userInfo = uni.getStorageSync('userInfo')
			let data = {
				content:this.keyword,
				name:userInfo.nickname,
				phone:userInfo.phone
			}
			garbageFeedback(data).then(res=>{
				if(res.code==1){
					this.isFadeback = true
				}
			})
		}
	}
}
